@import '../../global';
.contact {
  background-color: #fff;
  display: flex;
  @include mobile {
flex-direction: column;
  }

  .left {
    flex: 1;
    overflow: hidden;

    img {
      height: 90%;

    }
  }

  .right {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    h2 {
      font-size: 40px;
      @include mobile {
        font-size: 30px;
      }
    }


    form {
      width: 70%;
      height: 50%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
      @include mobile {
        width: 100%;
        height: 100%;
      }

      input {
        width: 300px;
        height: 30px;
        font-size: 14px;
      }
      textarea {
        width: 300px;
        height: 200px;
        font-size: 14px;
        @include mobile {
height: 150px;
        }
      }
      button {
        width: 150px;
        height: 30px;
        color: whitesmoke;
        background-color: crimson;
        font-size: 20px;
        border: none;
        border-radius: 10px;
        font-weight: 500;
        cursor: pointer;
      }
      span {
        color:green;

      }
    }
  }
}